<template>
  <Card class="shadow-lg h-full">
    <template #header>
      <CardHeader title="Customers" date="February, 10 2020"/>
    </template>
    <template v-if="getFormatted.items.length != 0">
      <div v-for="(customer, index) in getFormatted.items" class="flex flex-wrap justify-between items-center" :class="{ 'mb-3': index + 1 != getFormatted.items.length }" :key="index">
        <div class="flex flex-row items-center">
          <div>
            <b v-if="customer.customer" class="block text-teal-04">{{ customer.customer ? customer.customer.first_name : null }} {{ customer.customer ? customer.customer.last_name : null }}</b>
            <b v-else class="block text-red">No Name</b>
            <span class="text-sm text-dark">{{ customer.customer ? customer.customer.email : 'No Email' }}</span>
          </div>
        </div>
        <b class="text-dark">
          <Currency :value="parseFloat(customer.total).toFixed(2).toLocaleString()" />
        </b>
      </div>
    </template>
    <div v-else class="text-center">
      <p class="font-bold text-red mb-0">Nothing to see here</p>
    </div>
  </Card>
</template>

<script>
import { defineComponent } from 'vue'
import { useCustomers } from 'composable/dashboard'
import Card from 'bbUi/components/card/card.vue'
import CardHeader from 'components/dashboard/CardHeader.vue'
import Currency from 'components/Currency.vue'

export default defineComponent({
  components: {
    Card,
    CardHeader,
    Currency
  },
  async setup() {
    const { data, getFormatted, customers } = useCustomers()

    await customers()

    return {
      data,
      getFormatted
    }
  }
})
</script>